<template>
  <div class="user-profile">
    <van-nav-bar
      title=""
      left-arrow
      @click-left="$router.back()"
    ></van-nav-bar>
    <van-cell is-link title="" @click="onSelectFile">
      <van-image :src="user.photo" round class="avatar" />
    </van-cell>
    <input type="file" hidden name="" id="" ref="file" @change="onFileChange" />
    <van-cell
      is-link
      title=""
      :value="user.name"
      @click="isEditNameShow = true"
    ></van-cell>
    <van-cell is-link title="" value=""></van-cell>
    <van-cell
      is-link
      title=""
      :value="user.gender == 0 ? '' : ''"
      @click="isEditGenderShow = true"
    ></van-cell>
    <van-cell
      is-link
      title=""
      :value="user.birthday"
      @click="isEditBirthdayShow = true"
    ></van-cell>

    <!--  -->
    <van-popup v-model="isEditNameShow" position="bottom">
      <van-nav-bar
        title=""
        left-text=""
        right-text=""
        @click-left="isEditNameShow = false"
        @click-right="onUpDateName"
      ></van-nav-bar>
      <div>
        <van-field
          v-model="message"
          rows="2"
          autosize
          label=""
          type="textarea"
          maxlength="20"
          placeholder=""
          show-word-limit
        />
      </div>
    </van-popup>
    <!-- / -->

    <!--  -->
    <van-action-sheet
      v-model="isEditGenderShow"
      :actions="actions"
      cancel-text=""
      @cancel="isEditNameShow = false"
      @select="onGenderSelect"
    />
    <!-- / -->

    <!--  -->
    <van-popup v-model="isEditBirthdayShow" position="bottom">
      <van-datetime-picker
        :value="currentDate"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="isEditBirthdayShow=false"
        @confirm="onUpdateBirthday"
      />
    </van-popup>

    <!-- / -->
  </div>
</template>

<script>
import moment from 'moment'
import { ImagePreview } from 'vant'
import { getUserProfile, updataUserPhoto, updateUserProfile } from '@/api/user'
export default {
  name: 'UserProfile',
  components: {},
  props: {},
  data () {
    return {
      user: {},
      isEditNameShow: false,
      message: '',
      isEditGenderShow: false,
      isEditBirthdayShow: false,
      minDate: new Date(1950, 0, 1),
      maxDate: new Date(),
      // currentDate: new Date(),
      actions: [
        { name: '', value: 0 },
        { name: '', value: 1 }
      ]
    }
  },
  created () {
    this.loadUserProfile()
  },
  mounted () {},
  activited () {},
  update () {},
  beforeRouteUpdate () {},
  methods: {
    async loadUserProfile () {
      try {
        const { data } = await getUserProfile()
        this.user = data.data
        this.message = this.user.name
      } catch (error) {
        this.$toast.fail('')
      }
    },
    onSelectFile () {
      this.file.click()
    },
    onFileChange () {
      // 
      const that = this
      const filesObj = this.file.files[0]
      const fileData = URL.createObjectURL(filesObj)
      ImagePreview({
        images: [fileData],
        async onClose () {
          try {
            try {
              await this.$dialog.confirm({
                title: '',
                message: ''
              })
              this.$toast.loading({
                duration: 0,
                message: '...',
                forbidClick: true
              })

              // 
              const fd = new FormData()
              fd.append('photo', filesObj)
              await updataUserPhoto(fd)
              this.$toast.success('')
              that.user.photo = fileData
              that.$refs.file.value = ''
            } catch (error) {
              that.$refs.file.value = ''
            }
          } catch (error) {
            that.$refs.file.value = ''
            this.$toast.fail('')
          }
        }
      })
    },
    async updateUserProfile (field, value) {
      this.$toast.loading({
        duration: 0,
        message: '...',
        forbidClick: true
      })
      try {
        await updateUserProfile({
          [field]: value
        })
        this.isEditNameShow = false
        this.user.name = this.message
        this.$toast.success('')
      } catch (error) {
        this.isEditNameShow = false
        this.$toast.fail('')
        this.message = this.user.name
      }
    },
    async onUpDateName () {
      await this.updateUserProfile('name', this.message)
    },
    async onGenderSelect (data) {
      await this.updateUserProfile('gender', data.value)
      this.user.gender = data.value
      this.isEditGenderShow = false
    },
    onUpdateBirthday (value) {
      const date = moment(value).format('YYYY-MM-DD')
      this.updateUserProfile('birthday', date)
      this.user.birthday = date
      this.isEditBirthdayShow = false
    }
  },
  filter: {},
  computed: {
    file () {
      return this.$refs['file']
    },
    currentDate () {
      return new Date(this.user.birthday)
    }
  },
  watch: {}
}
</script>

<style lang="less" scoped>
.user-profile {
  .avatar {
    width: 30px;
    height: 30px;
  }
  .van-nav-bar {
    background-color: #fff;
  }
  .van-nav-bar__title {
    color: #323233;
  }
}
</style>
